.login-hero {
  background-color: #639dff;
  color: white;
  display: none !important;
  padding: 0 30px !important;
}
@media (min-width: 768px) {
  .login-hero {
    display: flex !important;
  }
}
@media (min-width: 1600px) {
  .login-hero {
    padding: 0 100px !important;
  }
}
.login-hero__title {
  font-size: 48px;
}
.login-hero__text {
  font-size: 18px;
}
.login-hero img {
  max-height: 400px;
  display: block;
  margin: 0 auto;
  max-width: 80%;
  margin-top: 30px;
}
@media (min-width: 1600px) {
  .login-hero img {
    max-width: none;
    max-height: none;
  }
}